'use client';

import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import { useUser } from '@/src/context/AuthContext';
import { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { Handshake, Users, DollarSign, FileText } from 'lucide-react';
import Link from 'next/link';

export default function AgentDashboard() {
  const { user } = useUser();
  const [commission, setCommission] = useState(0);
  const [referrals, setReferrals] = useState(0);
  const [activity, setActivity] = useState<string[]>([]);

  useEffect(() => {
    if (!user) return;

    const fetchReferrals = async () => {
      const q = query(collection(db, 'bookings'), where('agentId', '==', user.uid));
      const snap = await getDocs(q);
      setReferrals(snap.size);

      let total = 0;
      const logs: string[] = [];
      snap.forEach((doc) => {
        const data = doc.data();
        total += data.agentCommission || 0;
        logs.push(`Booking ${data.id || ''} - Commission $${data.agentCommission || 0}`);
      });
      setCommission(total);
      setActivity(logs.slice(0, 5));
    };

    fetchReferrals();
  }, [user]);

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <Header />

      <main className="flex-1 px-6 py-10 space-y-10">
        <h1 className="text-3xl font-bold text-center text-gray-800">ü§ù Agent Dashboard</h1>

        <section className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
          <DashboardCard icon={<Users />} title="Referrals" value={referrals.toString()} href="#" />
          <DashboardCard icon={<DollarSign />} title="Total Commission" value={`$${commission.toFixed(2)}`} href="#" />
          <DashboardCard icon={<Handshake />} title="Join New Program" value="Available" href="/my-business/programs" />
        </section>

        <section className="bg-white rounded-2xl p-6 border shadow">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">üìù Recent Activity</h2>
          <ul className="text-sm text-gray-700 space-y-2">
            {activity.length > 0 ? (
              activity.map((item, i) => (
                <li key={i} className="flex items-start gap-2">
                  <FileText className="mt-0.5" /> {item}
                </li>
              ))
            ) : (
              <p className="text-sm text-gray-500">No activity yet.</p>
            )}
          </ul>
        </section>

        <section className="bg-white rounded-2xl shadow p-6 border border-gray-200">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">‚ö° Quick Actions</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
            <QuickAction label="Refer a Customer" href="/rent" />
            <QuickAction label="View Commission History" href="/my-business/earnings" />
            <QuickAction label="Join New Program" href="/my-business/programs" />
          </div>
        </section>
      </main>

      <Footer />
    </div>
  );
}

function DashboardCard({ icon, title, value, href }: { icon: JSX.Element; title: string; value: string; href: string }) {
  return (
    <Link href={href} className="bg-white p-4 rounded-xl shadow hover:shadow-lg transition border border-gray-200 flex items-center gap-4">
      <div className="text-[#00d289] bg-[#e6fff5] rounded-full p-2">
        {icon}
      </div>
      <div>
        <p className="text-sm text-gray-500">{title}</p>
        <h3 className="text-lg font-bold text-gray-800">{value}</h3>
      </div>
    </Link>
  );
}

function QuickAction({ label, href }: { label: string; href: string }) {
  return (
    <Link href={href} className="block bg-[#00d289] hover:bg-[#00b67a] text-white text-center font-medium px-4 py-3 rounded-xl transition">
      {label}
    </Link>
  );
}
