'use client';

import { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { useUser } from '@/src/context/AuthContext';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import Link from 'next/link';
import { Bike, FileText, LineChart, Star } from 'lucide-react';

export default function PrivateProviderDashboard() {
  const { user } = useUser();
  const [ebikesCount, setEbikesCount] = useState(0);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      if (!user?.uid) return;
      const q = query(collection(db, 'rentalCompanies'), where('ownerId', '==', user.uid));
      const companySnap = await getDocs(q);
      if (companySnap.empty) return;

      const companyId = companySnap.docs[0].id;
      const ebikeSnap = await getDocs(query(collection(db, 'ebikes'), where('companyId', '==', companyId)));
      setEbikesCount(ebikeSnap.size);
      setLoading(false);
    };

    fetchData();
  }, [user]);

  if (loading) return <div className="text-center py-10">Loading...</div>;

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <Header />

      <main className="flex-1 px-6 py-10 space-y-10">
        <h1 className="text-3xl font-bold text-center text-gray-800">üö¥ Private Provider Dashboard</h1>

        <section className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
          <DashboardCard icon={<Bike />} title="My eBikes" value={ebikesCount.toString()} href="/ebikeManagement" />
          <DashboardCard icon={<Star />} title="Ratings & Reviews" value="4.7/5" href="/reviews" />
          <DashboardCard icon={<LineChart />} title="Earnings Overview" value="$1,280" href="/reports" />
        </section>

        <section className="bg-white rounded-2xl shadow p-6 border border-gray-200">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">‚ö° Quick Actions</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
            <QuickAction label="Add New eBike" href="/ebikeManagement" />
            <QuickAction label="Check Reviews" href="/reviews" />
            <QuickAction label="View Reports" href="/reports" />
          </div>
        </section>

        <section className="bg-white rounded-2xl p-6 border shadow">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">üìù Recent Activity</h2>
          <ul className="text-sm text-gray-700 space-y-2">
            <li className="flex items-start gap-2"><FileText className="mt-0.5" /> eBike "VN-0123" marked as available</li>
            <li className="flex items-start gap-2"><FileText className="mt-0.5" /> New customer review received</li>
            <li className="flex items-start gap-2"><FileText className="mt-0.5" /> Maintenance log updated for eBike "VN-0088"</li>
          </ul>
        </section>
      </main>

      <Footer />
    </div>
  );
}

function DashboardCard({ icon, title, value, href }: { icon: JSX.Element; title: string; value: string; href: string }) {
  return (
    <Link href={href} className="bg-white p-4 rounded-xl shadow hover:shadow-lg transition border border-gray-200 flex items-center gap-4">
      <div className="text-[#00d289] bg-[#e6fff5] rounded-full p-2">
        {icon}
      </div>
      <div>
        <p className="text-sm text-gray-500">{title}</p>
        <h3 className="text-lg font-bold text-gray-800">{value}</h3>
      </div>
    </Link>
  );
}

function QuickAction({ label, href }: { label: string; href: string }) {
  return (
    <Link href={href} className="block bg-[#00d289] hover:bg-[#00b67a] text-white text-center font-medium px-4 py-3 rounded-xl transition">
      {label}
    </Link>
  );
}
