// app/my-business/rental-company/page.tsx
'use client';

import Link from 'next/link';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import { DollarSign, Bike, MapPin, Users, LineChart, FileText } from 'lucide-react';
import { useEffect, useState } from 'react';
import { db } from '@/src/firebaseConfig';
import { collection, query, where, getDocs } from 'firebase/firestore';
import { useUser } from '@/src/context/AuthContext';

export default function RentalCompanyDashboard() {
  const { user } = useUser();
  const [stationCount, setStationCount] = useState(0);
  const [ebikeCount, setEbikeCount] = useState(0);
  const [staffCount, setStaffCount] = useState(0);

  useEffect(() => {
    const fetchData = async () => {
      if (!user?.uid) return;

      const companySnap = await getDocs(
        query(collection(db, 'rentalCompanies'), where('ownerId', '==', user.uid))
      );

      if (!companySnap.empty) {
        const companyId = companySnap.docs[0].id;

        const stationSnap = await getDocs(
          query(collection(db, 'rentalStations'), where('companyId', '==', companyId))
        );
        setStationCount(stationSnap.size);

        const ebikeSnap = await getDocs(
          query(collection(db, 'ebikes'), where('companyId', '==', companyId))
        );
        setEbikeCount(ebikeSnap.size);

        const staffSnap = await getDocs(
          query(collection(db, 'staffs'), where('companyId', '==', companyId))
        );
        setStaffCount(staffSnap.size);
      }
    };

    fetchData();
  }, [user]);

  return (
    <div className="min-h-screen flex flex-col bg-gray-50">
      <Header />

      <main className="flex-1 px-6 py-10 space-y-10">
        <h1 className="text-3xl font-bold text-center text-gray-800">üè¢ Rental Company Dashboard</h1>

        <section className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-4 gap-6">
          <DashboardCard icon={<MapPin />} title="Stations" value={String(stationCount)} href="/my-business/stations" />
          <DashboardCard icon={<Bike />} title="eBikes" value={String(ebikeCount)} href="/ebikeManagement" />
          <DashboardCard icon={<DollarSign />} title="Revenue (This Month)" value="$2,340" href="/my-business/revenue" />
          <DashboardCard icon={<Users />} title="Staff" value={String(staffCount)} href="/my-business/rental-company/staff" />
        </section>

        <section className="grid grid-cols-1 xl:grid-cols-2 gap-6">
          <div className="bg-white rounded-2xl p-6 border shadow">
            <h2 className="text-xl font-semibold text-gray-800 mb-4">üìà Revenue Overview</h2>
            <div className="h-48 flex items-center justify-center text-gray-400">
              <LineChart className="w-16 h-16" />
              <p className="ml-4">Revenue chart coming soon</p>
            </div>
          </div>

          <div className="bg-white rounded-2xl p-6 border shadow">
            <h2 className="text-xl font-semibold text-gray-800 mb-4">üìù Recent Activity</h2>
            <ul className="text-sm text-gray-700 space-y-2">
              <li className="flex items-start gap-2"><FileText className="mt-0.5" /> New booking received from John Doe</li>
              <li className="flex items-start gap-2"><FileText className="mt-0.5" /> Station "My Khe Beach" updated</li>
              <li className="flex items-start gap-2"><FileText className="mt-0.5" /> eBike "VIN DE0183" marked as under maintenance</li>
              <li className="flex items-start gap-2"><FileText className="mt-0.5" /> New model "EMOVE Flex" added</li>
            </ul>
          </div>
        </section>

        <section className="bg-white rounded-2xl shadow p-6 border border-gray-200">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">‚ö° Quick Actions</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
            <QuickAction label="Add New Station" href="/my-business/stations" />
            <QuickAction label="Create eBike Model" href="/ebikeManagement" />
            <QuickAction label="Assign Staff" href="/my-business/rental-company/staff" />
            <QuickAction label="View Bookings" href="/bookings" />
            <QuickAction label="Upload Invoice" href="/my-business/documents" />
            <QuickAction label="View Reports" href="/reports" />
          </div>
        </section>
      </main>

      <Footer />
    </div>
  );
}

function DashboardCard({ icon, title, value, href }: { icon: JSX.Element; title: string; value: string; href: string }) {
  return (
    <Link href={href} className="bg-white p-4 rounded-xl shadow hover:shadow-lg transition border border-gray-200 flex items-center gap-4">
      <div className="text-[#00d289] bg-[#e6fff5] rounded-full p-2">
        {icon}
      </div>
      <div>
        <p className="text-sm text-gray-500">{title}</p>
        <h3 className="text-lg font-bold text-gray-800">{value}</h3>
      </div>
    </Link>
  );
}

function QuickAction({ label, href }: { label: string; href: string }) {
  return (
    <Link href={href} className="block bg-[#00d289] hover:bg-[#00b67a] text-white text-center font-medium px-4 py-3 rounded-xl transition">
      {label}
    </Link>
  );
}
