"use client";

// ‚úÖ Trang ch·ªçn lo·∫°i h√¨nh doanh nghi·ªáp (rental / private / agent)

import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { getDocs, query, where, collection } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { useUser } from '@/src/context/AuthContext';
import RentalCompanyDashboard from '@/src/components/my-business/dashboards/RentalCompanyDashboard';
import PrivateProviderDashboard from '@/src/components/my-business/dashboards/PrivateProviderDashboard';
import AgentDashboard from '@/src/components/my-business/dashboards/AgentDashboard';

export default function MyBusinessPage() {
  const { user } = useUser();
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [businessType, setBusinessType] = useState<
    'rental_company' | 'private_provider' | 'agent' | null
  >(null);

  useEffect(() => {
    const fetchBusinessType = async () => {
      if (!user?.uid) return;

      const rentalQuery = query(
        collection(db, 'rentalCompanies'),
        where('ownerId', '==', user.uid)
      );
      const agentQuery = query(
        collection(db, 'agents'),
        where('ownerId', '==', user.uid)
      );

      const [rentalSnap, agentSnap] = await Promise.all([
        getDocs(rentalQuery),
        getDocs(agentQuery),
      ]);

      if (!rentalSnap.empty) {
        const data = rentalSnap.docs[0].data();
        setBusinessType(data.businessType);
      } else if (!agentSnap.empty) {
        setBusinessType('agent');
      } else {
        router.replace('/my-business/create');
      }

      setLoading(false);
    };

    fetchBusinessType();
  }, [user, router]);

  if (loading) return <div className="text-center py-10">Loading...</div>;

  if (businessType === 'rental_company') return <RentalCompanyDashboard />;
  if (businessType === 'private_provider') return <PrivateProviderDashboard />;
  if (businessType === 'agent') return <AgentDashboard />;

  return null;
}
